.size-category {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-bottom: 2rem;
}
@media (max-width: 991px) {
  .product-right .size-text {
    text-align: center;
  }
}
@media (max-width: 600px) {
  .product-right h6 {
    font-size: 90% !important;
  }

  .service-list {
    font-size: 80% !important;
  }
}

