.border-red {
  border: 1px solid orangered !important;
  border-radius: 0% !important;
  padding: 0.1rem;
  width: fit-content;
  height: fit-content;
}
.border-gray {
  border: 1px solid rgb(219, 217, 217) !important;
  border-radius: 0% !important;
  padding: 0.1rem;
  width: fit-content;
  height: fit-content;
}

.shipping-method {
  display: flex;
  flex-direction: column;
}

.color-variant-image {
  width: 100%;
  padding: 2px;
}
.service-list {
  margin-bottom: 1rem !important;
  text-align: start;
}
.service-list span {
  font-size: 130%;
  color: darkgreen;
}

.price-range-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 5px;
}

.price-range-container .price-box {
  border: 0.1px solid lightgray;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 5rem;
  width: 13rem;
}

.price-range-container .price-box h3 {
  margin-top: 5px;
}

.price-range-container .price-box.selected {
  background: #ff4c3b;
}

.price-range-container .price-box.selected h3 {
  color: white;
}
.price-range-container .price-box.selected p {
  color: white;
}

.product-right h3 {
  font-size: 26px;
  color: darkgoldenrod;
  margin-bottom: 15px;
  font-weight: bold;
}
