@media (min-width: 1100px) {
  .product-container-section {
    max-width: 70%;
    margin-left: 12rem;
    background-color: rgb(255, 255, 255);
    margin-top: 2rem;
    margin-bottom: 3rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 7px;
  }
}

